import { ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IMapKeyValue, IOptionsPsActionHyperlink, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { CommonBussinessConstant } from '../../psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from '../../psServices/models/ps-commonBusiness.settings';
import { IOptionsPsownershipListExposed } from './ps-ownership-list.component.interface';

/**
 * @author Ahmed.Elfkhrany
 * @since 27/09/2022
 *
 */
@Component({
  selector: 'ps-ownership-list',
  templateUrl: './ps-ownership-list.component.html',
  styleUrls: ['./ps-ownership-list.component.scss'],
})
export class PsOwnershipListComponent extends PsTemplateListPage implements OnInit {
  @Input() options: IOptionsPsownershipListExposed;
  @Output() public onClickCard = new EventEmitter<any>();
  @Output() reloadFct = new EventEmitter<string>();


  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  private showSingleAccount = false;
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();

  public addOwnerOptions: IOptionsPsActionHyperlink = {
    route: 'add-owner',
    labelKey: 'add_owner_key',
    iconOptions: {
      iconName: 'owner',
      labelOptions: {
        labelKey: 'add_owner_key'
      }
    },
    pageOptions: {
      operId: CommonBussinessConstant.ADD_OWNER_OPER_ID,
      title: 'add_owner_key',
      iconName: 'owners',
    }
  };
  constructor(private cdRef?: ChangeDetectorRef) {
    super();
  }

  ngOnInit() {
    super.init();
    this.populateHeaderValueMap();
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }
    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }

  }

  getContainerLookUpOptions(val) {
    return this.prepareLookUp(val);
  }

  private prepareLookUp(card: any): any {
    const lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.currencyFlag = 'currencyBriefNameEnglish';
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.itemList = this.options.itemList;
    lookupDetails.isEditable = true;
    lookupDetails.translateSubTitle = this.options.translate;
    lookupDetails.matCardCssSelectors = card.matCardCssSelectors;
    lookupDetails.requestMap = this.requestMap;
    lookupDetails.pageData = {
      operID: CommonBussinessConstant.OWNERS_LIST_OPER_ID,
      title: 'owners_list_report_key'
    };

    lookupDetails.actionDetailsOptions = [
      {
        id: 'delete',
        buttonIcon: 'delete',
        labelOptions: {
          labelKey: 'delete_key'
        },
        actionUrl: PsCommonBusinessSettings.serviceUrl.deleteOwnerEndPoint,
        cssClass: 'delete-benef fab-edit-button',
        label: 'delete_key',
        iconName: 'trash',
        actionType: 'delete',
        alertType: 'prompt',
        alertStyle: 'warning',
        alertLabel: 'are_you_sure_you_want_to_delete_the_related_owner_key',
        reloadList: true
      },
      {
        id: 'edit',
        label: 'edit_key',
        actionHyperlink: {
          group: this.options.group,
          iconOptions: {
            iconName: 'edit1',
            labelOptions: {
              labelKey: 'edit_key'
            }
          },
          route: 'add-owner',
          navigationOptions: {
            queryParams: { readOnlypage: false },
          },
          pageOptions: {
            operId: CommonBussinessConstant.ADD_OWNER_OPER_ID,
            iconName: 'edit1',
            title: 'edit_key'
          }
        },
        cssClass: 'edit-owner'
      },

    ];

    //Added by Marina TP:#1533524 to be able to reload owners list after delete
    lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }
    return lookupDetails;
  }
  reloadList() {
    this.reloadFct.emit();
  }
  private populateHeaderValueMap() {
    let entityTypeMap: IMapKeyValue = {};
    let idTypeMap: IMapKeyValue = {};
    let idNumberMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};
    let headerSubtitleMap: IMapKeyValue = {};
    let designationMap: IMapKeyValue = {};
    let ownershipPercentageMap: IMapKeyValue = {};
    let statusMap: IMapKeyValue = {};

    entityTypeMap = {
      key: 'entityTypeDescriptionConcat',
      value: '',
    };

    idTypeMap = {
      key: 'idTypeDescriptionConcat',
      value: ''
    };

    idNumberMap = {
      key: 'idNumber',
      value: ''
    };

    designationMap = {
      key: 'designationDescriptionConcat',
      value: ''
    };

    ownershipPercentageMap = {
      key: 'ownershipPercentage',
      value: ''
    };
    statusMap = {
      key: 'statusDesc',
      value: ''
    };


    headerTitleMap = {
      key: 'title',
      value: 'title',
      isEdit: false,

    };

    // headerSubtitleMap = {
    //   key: 'subTitle',
    //   value: 'cifNumber',

    //   isEdit: false,

    // };

    // key here will be used as label in look up details component
    this.labelValuesMap.set('entity_type_key', entityTypeMap);
    // this.labelValuesMap.set('currency_key', designationMap);
    this.labelValuesMap.set('id_type_key', idTypeMap);
    this.labelValuesMap.set('id_number_key', idNumberMap);
    this.labelValuesMap.set('designation_key', designationMap);
    this.labelValuesMap.set('ownershipPercentage', ownershipPercentageMap);
    this.labelValuesMap.set('status', statusMap);

    this.headerMap.set('title', headerTitleMap,);
    // this.headerMap.set('subTitle', headerSubtitleMap);

  }

  updateCard(card: any) {
    if (card && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(acct => acct === card);
      this.options.item = this.options.itemList[0];
      this.showSingleAccount = !this.showSingleAccount;
      this.options.showItemPopUp = !this.options.showItemPopUp;
    }
    this.onClickCard.emit(this.options);
  }


  ngAfterViewChecked() {
    this.cdRef.markForCheck();
  }

}
